<template>
    <div class="itembox" @click="clickhandler">
        <div class="icon"></div>
        <div>
            <ul>
                <li class="text"><span>{{this.group==true?'(群)':' '}}{{ this.name }}</span></li>
                <li>{{ this.id }}</li>
            </ul>
        </div>
    </div>
</template>
<script>

export default {
    props:{
        id:{type:String},
        name:{type:String},
        group:{type:Boolean}
    },
    methods:{
        clickhandler(){
            let param = {
                id :this.id,
                group:this.group 
            }
            this.$emit("switchDialog",param)
        }
    }
}
</script>
<style scoped>
.itembox{
    display: flex;
    width:100%;
    height: 80px;
    background-color: rgb(234, 0, 0);
    margin:6px 0;
    padding:4px;
}
.itembox:hover{
    transform: rotateY(15deg);
    background-color:rgb(254, 76, 76) ;
}
.text{
  -webkit-text-stroke: 1px black;
  -webkit-text-fill-color :white;
  font-weight: bold;
  font-size:30px;
  color:#fff;
}
</style>